.intro{
    background-color: white;
    display: flex;
    .left{

        flex: 0.4;//divide page into columns
        overflow: hidden;

        .imgContainer{
            width: 700px;
            height: 700px;
            background-color: crimson;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;
            
            img{
               height: 90%;
            }
        }
    }
    .right{
        flex: 0.6;
        position: relative;
        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            h1{
                font-size: 60px;
                margin: 10px 0;
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;
                
                span{
                    font-size: inherit;
                    color: crimson;
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 20%;
            
            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}